export default {
	data() {
		return {
			asd: '1000000000184812',
			data: {},
			shulinag: 1,
			ProductId: '',
			PartyA:false,
			productCodce: '',
		};
	},
	 onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
	            console.log(option); //打印出上个页面传递的参数。
				if(option.code){
					this.productCodce = option.code
				}else{
					uni.showToast({
						title: '未获取到值',
						icon: "none"
					})
				}
				
				
	        },
	mounted() {
		//获取商品信息
		console.log(this.$store.state.buyids)
		uni.request({
			url: this.$store.state.url + 'Product/GetProductMini',
			method: 'GET',
			data: {
				productCodce: this.$store.state.buyids || this.productCodce
			},
			header: {
				'Token': this.$store.state.token
			},
			success: (res) => {
				if (JSON.parse(res.data).code == 0 || JSON.parse(res.data).Code == 0) {
					console.log(JSON.parse(res.data).Data)
					this.data = JSON.parse(res.data).Data
				} else {
					uni.showToast({
						title: JSON.parse(res.data).Data,
						icon: "none"
					})
				}
			}
		});

	},
	methods: {
		//修改状态
		alls:function(){
			this.PartyA = !this.PartyA
		},
		//增加商品数量
		jia: function() {
			this.shulinag += 1
		},
		//减少商品数量
		jian: function() {
			if (this.shulinag == 1) {
				uni.showToast({
					title: '商品数量最少为 1',
					icon: 'none'
				})
			} else {
				this.shulinag -= 1
			}
		},
		//打开购物车
		Cart: function() {
			uni.navigateTo({
				url: "./ShoppingCart",
			});
		},
		//加入购物车
		joinCart: function() {
			uni.request({
				url: this.$store.state.url + 'CRM/AddToShopCar',
				method: 'POST',
				data: {
					CustomerId: this.$store.state.Customercode, //客户id
					ProductId: this.data.ProductId, //产品id
					Number: this.shulinag,
					IsAssembly: this.PartyA, //甲方安装
				},
				header: {
					'Token': this.$store.state.token
				},
				success: (res) => {
					if (JSON.parse(res.data).code == 0 || JSON.parse(res.data).Code == 0) {
						console.log(JSON.parse(res.data))
					} else {
						uni.showToast({
							title: JSON.parse(res.data).Data,
							icon: "none"
						})
					}
				}
			});

		},
		//立即购买
		buy: function() {
			var asd = {}
			this.$store.state.Buy = []
			asd.mani = this.data.UnitPrice,     //商品价格
			asd.title = this.data.Describe,     // 商品标题
			asd.ProductId = this.data.ProductId, // 商品id
			asd.Number = this.shulinag,          // 商品数量
			asd.IsAssembly = this.PartyA,        // 是否甲方安装
			
			this.$store.state.Buy[0] = asd
			console.log(this.$store.state.Buy)
			uni.navigateTo({
				url: "./Buy",
			});
		}
	}
}
